export function createNode(flowElement) {
    return {
        id: flowElement.Id,
        type: NodeType__toString(new NodeType(2)),
        data: {
            label: flowElement.Descr,
        },
        style: {
            background: "red",
            color: "white",
            border: "1px solid #222138",
            width: 180,
        },
        position: {
            x: 700,
            y: 50,
        },
    };
}

export function update(msg, model) {
    const flowElement = msg.fields[0];
    const newNodes = concat([model.NodeList, singleton(createNode(flowElement))]);
    return [new Model(newNodes), Cmd_none()];
}

